html {
  height: 100%; }

body {
  font-family: 'Open Sans';
  color: white;
  min-height: 100%;
  background: -webkit-radial-gradient(#2b1339, #380c77);
  background: radial-gradient(#2b1339, #380c77); }

body.landing {
  background: #5b5579; }

.hero-content {
  background: #2f1d57;
  padding: 100px; }

.hero-content h1 {
  font-size: 70px;
  margin-bottom: 50px;
  font-weight: 300; }

.selling-points {
  padding: 100px; }

.selling-points .point {
  width: 32%;
  float: left;
  padding: 0 0.5%; }

.selling-points .point h5 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
  height: 85px; }

.selling-points .point p {
  font-size: 14px; }

.nav-bar {
  background: #2c1231;
  height: 90px;
  padding: 0 100px; }

.collection-album-container img {
  max-width: 100%; }

.collection-album-container .collection-album-image-container {
  position: relative; }

.collection-album-container .collection-album-image-overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 10; }

.collection-album-container .collection-overlay-content {
  position: absolute;
  bottom: 20px;
  width: 100%;
  text-align: center; }

.collection-album-container .collection-overlay-button {
  font-size: 20px;
  background-color: white;
  color: #d2287b;
  border-radius: 50% 50%;
  display: inline-block;
  width: 42px;
  height: 42px; }

.collection-album-container .collection-overlay-button:hover {
  cursor: pointer;
  color: white;
  background-color: #d2287b; }

.collection-album-container .collection-overlay-button i {
  line-height: 42px; }

.collection-album-container .collection-overlay-button .fa-play {
  padding-left: 5px; }
